/* Menu Button */
.menu-button {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1000;
  width: auto;
  margin: 1em;
  padding: 10px 15px;
  text-align: center;
  font-size: 1.4em;
  border: none;
  color: #fff;
  background-color: #373a3c;
  text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.4); }

.menu-button:hover {
  opacity: 0.6; }

.cb-slideshow,
.cb-slideshow:after {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 0; }

.cb-slideshow:after {
  content: '';
  background: transparent url(../images/pattern.png) repeat;
  opacity: 0.3; }

@keyframes imageAnimation {
  0% {
    opacity: 0;
    animation-timing-function: ease-in; }
  8% {
    opacity: 1;
    animation-timing-function: ease-out; }
  17% {
    opacity: 1; }
  25% {
    opacity: 0; }
  100% {
    opacity: 0; } }

.cb-slideshow li span {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  color: transparent;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: none;
  opacity: 0;
  z-index: 0;
  animation: imageAnimation 24s linear infinite 0s; }

.cb-slideshow li:nth-child(2) span {
  background-image: url(../images/header/bg2.jpg);
  animation-delay: 6s; }

.cb-slideshow li:nth-child(3) span {
  background-image: url(../images/header/bg3.jpg);
  animation-delay: 12s; }

.cb-slideshow li:nth-child(4) span {
  background-image: url(../images/header/bg4.jpg);
  animation-delay: 18s; }

.no-cssanimations .cb-slideshow li span {
  opacity: 1; }

/* Close Button */
.close-button {
  width: 16px;
  height: 16px;
  position: absolute;
  right: 1em;
  top: 1em;
  overflow: hidden;
  text-indent: 16px;
  border: none;
  z-index: 1001;
  background: transparent;
  color: transparent; }

.close-button::before,
.close-button::after {
  content: '';
  position: absolute;
  width: 2px;
  height: 100%;
  top: 0;
  left: 50%;
  background: #95a5a6; }

.close-button::before {
  transform: rotate(45deg); }

.close-button::after {
  transform: rotate(-45deg); }

